import React, { useRef, useState } from "react";
import "react-quill/dist/quill.snow.css";
import { Button, Col, FormGroup, Input, Row } from "reactstrap";
import { useDispatch, useSelector } from "react-redux";
import { updateHomeReducer } from "../reducers/homeReducer";
import { useNavigate } from "react-router-dom";

const EditServices = (props) => {
  const navigate = useNavigate();
  const [services, setServices] = useState(props.homeData.services);
  let homeData = props.homeData;

  const dispatch = useDispatch();
  const { userdetails } = useSelector((state) => state.user);
  const [message, setMessage] = useState("");

  const cancelEditServices = () => {
    props.cancelEditServices(false);
  };

  const updateServices = async () => {
    try {
      const { data } = await dispatch(
        updateHomeReducer({
          ...homeData,
          services: { ...services },
          token: userdetails.token,
        })
      );

      props.cancelEditServices(false);
    } catch (err) {
      setMessage(err.response.data.message);
    }
  };

  return (
    <>
      <Row>
        <Col xs={6}>Active : </Col>
        <Col xs={6} className="d-flex justify-content-between">
          <FormGroup switch>
            <Input
              role="switch"
              type="switch"
              checked={services.active}
              onChange={() => {
                setServices({
                  ...services,
                  active: !services.active,
                });
              }}
            />
          </FormGroup>
          <button
            type="button"
            className="btn btn-link py-0 text-decoration-none text-uppercase"
            onClick={() => navigate("/profile/services")}
          >
            View/Edit
          </button>
        </Col>
      </Row>
      <Row>
        <Col></Col>
      </Row>
      <div>
        <Row>
          <Col className="text-end d-sm-flex d-md-block">
            <Button
              className="sp-btn-secondary rounded-0 px-5 me-2"
              onClick={cancelEditServices}
            >
              Cancel
            </Button>
            <Button
              className="sp-btn-secondary rounded-0 px-5"
              onClick={updateServices}
            >
              Update
            </Button>
          </Col>
        </Row>
      </div>
    </>
  );
};

export default EditServices;
