import React, { useRef, useState } from "react";
import "react-quill/dist/quill.snow.css";
import { Button, Col, FormGroup, Input, Row } from "reactstrap";
import { useDispatch, useSelector } from "react-redux";
import { updateHomeReducer } from "../reducers/homeReducer";

const EditSkills = (props) => {
  const [skills, setSkills] = useState(props.homeData.skills);
  let homeData = props.homeData;

  const dispatch = useDispatch();
  const { userdetails } = useSelector((state) => state.user);
  const [message, setMessage] = useState("");

  const cancelEditSkills = () => {
    props.cancelEditSkills(false);
  };

  const updateSkills = async () => {
    try {
      const { data } = await dispatch(
        updateHomeReducer({
          ...homeData,
          skills: { ...skills },
          token: userdetails.token,
        })
      );

      props.cancelEditSkills(false);
    } catch (err) {
      setMessage(err.response.data.message);
    }
  };

  return (
    <>
      <Row>
        <Col md={6}>Active : </Col>
        <Col md={6}>
          <FormGroup switch>
            <Input
              role="switch"
              type="switch"
              checked={skills.active}
              onChange={() => {
                setSkills({
                  ...skills,
                  active: !skills.active,
                });
              }}
            />
          </FormGroup>
        </Col>
      </Row>
      <div>
        <Row>
          <Col className="text-end">
            <Button
              className="sp-btn-secondary rounded-0 px-5 mx-2"
              onClick={cancelEditSkills}
            >
              Cancel
            </Button>
            <Button
              className="sp-btn-secondary rounded-0 px-5"
              onClick={updateSkills}
            >
              Update
            </Button>
          </Col>
        </Row>
      </div>
    </>
  );
};

export default EditSkills;
