import React from "react";
import { Col, Row } from "reactstrap";
import parse from "html-react-parser";

const MiddleBanner = ({ homeData }) => {
  const middleBanner = homeData.middleBanner;
  return (
    <>
      <Row>
        <Col sx={6}>Status</Col>
        <Col sx={6}>{middleBanner.active ? "Active" : "Inactive"}</Col>
      </Row>
      <Row>
        <Col sx={6}>Banner Content</Col>
        <Col sx={6}>{parse(`${middleBanner.bannerText}`)}</Col>
      </Row>
      <Row>
        <Col sx={6}>Banner Image</Col>
        <Col sx={6}>
          {middleBanner.bannerImage ? (
            <img
              src={`/uploads/${middleBanner.bannerImage}`}
              alt={middleBanner.bannerText}
              style={{ maxHeight: "80px" }}
            />
          ) : (
            ""
          )}
        </Col>
      </Row>
    </>
  );
};

export default MiddleBanner;
