import React from "react";
import { Col, Row } from "reactstrap";
import parse from "html-react-parser";

const HomeContent1 = ({ homeData }) => {
  const pageContent1 = homeData.pageContent1;
  return (
    <>
      <Row>
        <Col sx={6}>Status</Col>
        <Col sx={6}>{pageContent1.active ? "Active" : "Inactive"}</Col>
      </Row>
      <Row>
        <Col sx={6}>Content Title</Col>
        <Col sx={6}>{parse(`${pageContent1.contentTitle}`)}</Col>
      </Row>
      <Row>
        <Col sx={6}>Content Heading</Col>
        <Col sx={6}>{parse(`${pageContent1.contentHeading}`)}</Col>
      </Row>
      <Row>
        <Col sx={6}>Description</Col>
        <Col sx={6}>{parse(`${pageContent1.contentData}`)}</Col>
      </Row>
      <Row>
        <Col sx={6}>Attachment</Col>
        <Col sx={6}>
          {pageContent1.attachment ? (
            <a
              href={`/uploadedFiles/${pageContent1.attachment}`}
              alt={pageContent1.contentHeading}
              style={{ maxHeight: "80px" }}
            >
              Download Attachment
            </a>
          ) : (
            ""
          )}
        </Col>
      </Row>
    </>
  );
};

export default HomeContent1;
