import parse from "html-react-parser";
import React from "react";
import { Col, Row } from "reactstrap";

const UserProfile = ({ userProfile }) => {
  return (
    <>
      <Row>
        <Col>
          {userProfile.pic ? (
            <img
              src={`/uploads/${userProfile.pic}`}
              alt={userProfile.name}
              className="profile_pic"
            />
          ) : (
            <img
              src="/images/user-avatar.jpg"
              alt={userProfile.name}
              className="profile_pic"
            />
          )}
        </Col>
      </Row>
      <Row>
        <Col md={6}>Name : </Col>
        <Col md={6}>{userProfile.name}</Col>
      </Row>
      <Row>
        <Col md={6}>Email Id : </Col>
        <Col md={6}>{userProfile.email}</Col>
      </Row>
      <Row>
        <Col md={6}>Mobile : </Col>
        <Col md={6}>
          {userProfile.mobile} , {userProfile.mobile2}
        </Col>
      </Row>
      <Row>
        <Col md={6}>D.O.B : </Col>
        <Col md={6}>{userProfile.dob}</Col>
      </Row>
      <Row>
        <Col md={6}>Language : </Col>
        <Col md={6}>{userProfile.language}</Col>
      </Row>
      <Row>
        <Col md={6}>Strength : </Col>
        <Col md={6}>{userProfile.strength}</Col>
      </Row>
      <Row>
        <Col md={6}>Skype Id : </Col>
        <Col md={6}>{userProfile.skypeId}</Col>
      </Row>
      <Row>
        <Col md={6}>Permanent Address : </Col>
        <Col md={6}>{parse(`${userProfile.permanentAddress}`)}</Col>
      </Row>
      <Row>
        <Col md={6}>Career Objectives : </Col>
        <Col md={6}>{parse(`${userProfile.careerObjective}`)}</Col>
      </Row>
      <Row>
        <Col md={6}>Career Summary : </Col>
        <Col md={6}>{parse(`${userProfile.careerSummary}`)}</Col>
      </Row>
      <Row>
        <Col md={6}>Personal Qualities : </Col>
        <Col md={6}>{parse(`${userProfile.personalQualities}`)}</Col>
      </Row>
    </>
  );
};

export default UserProfile;
