import React, { useRef, useState } from "react";
import ReactQuill from "react-quill";
import EditorToolbar, { modules, formats } from "../utils/QuillToolbar";
import "react-quill/dist/quill.snow.css";
import { Button, Col, FormGroup, Input, Row } from "reactstrap";
import { useDispatch, useSelector } from "react-redux";
import { updateHomeReducer } from "../reducers/homeReducer";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faRemove, faRotateBack } from "@fortawesome/free-solid-svg-icons";
import axios from "axios";
const API_URL = process.env.REACT_APP_API_URL
  ? process.env.REACT_APP_API_URL
  : "";
const EditHomeContent2 = (props) => {
  const [pageContent2, setPageContent2] = useState(props.homeData.pageContent2);
  let homeData = props.homeData;

  const dispatch = useDispatch();
  const { userdetails } = useSelector((state) => state.user);
  const [file, setFile] = useState(null);
  const fileInputRef = useRef(null);
  const [message, setMessage] = useState("");

  const updateContent1Data = async (e, field) => {
    if (field) {
      await setPageContent2({
        ...pageContent2,
        [field]: e,
      });
    } else {
      await setPageContent2({
        ...pageContent2,
        [e.target.id]: e.target.value,
      });
    }
  };

  const cancelEditContent2 = () => {
    props.cancelEditContent2(false);
  };

  const updateHomeContent1 = async () => {
    const formData = new FormData();
    formData.append("image", file);
    try {
      if (file) {
        const res = await axios.post(`${API_URL}/api/upload/`, formData, {
          headers: {
            "Content-Type": "multipart/form-data",
            token: userdetails.token,
          },
        });
        await setPageContent2({
          ...pageContent2,
          attachment: res.data.filename,
        });
        const { data } = await dispatch(
          updateHomeReducer({
            ...homeData,
            pageContent2: {
              ...pageContent2,
              attachment: res.data.filename,
            },
            token: userdetails.token,
          })
        );
      } else {
        const { data } = await dispatch(
          updateHomeReducer({
            ...homeData,
            pageContent2: { ...pageContent2 },
            token: userdetails.token,
          })
        );
      }

      props.cancelEditContent2(false);
    } catch (err) {
      setMessage(err.response.data.message);
    }
  };

  const handleFileChange = (event) => {
    setFile(event.target.files[0]);
  };
  const handleFileRemove = (event) => {
    setFile(null);
    fileInputRef.value = null;
  };

  return (
    <>
      <Row>
        <Col md={6}>Active : </Col>
        <Col md={6}>
          <FormGroup switch>
            <Input
              role="switch"
              type="switch"
              checked={pageContent2.active}
              onChange={() => {
                setPageContent2({
                  ...pageContent2,
                  active: !pageContent2.active,
                });
              }}
            />
          </FormGroup>
        </Col>
      </Row>

      <Row>
        <Col md={6}>Content Title : </Col>
        <Col md={6}>
          <EditorToolbar toolbarId={"contentTitle"} />
          <ReactQuill
            theme="snow"
            defaultValue={pageContent2.contentTitle}
            onChange={(e) => updateContent1Data(e, "contentTitle")}
            placeholder={"Write something awesome..."}
            modules={modules("contentTitle")}
            formats={formats}
          />
        </Col>
      </Row>
      <Row>
        <Col md={6}>Content Heading : </Col>
        <Col md={6}>
          <EditorToolbar toolbarId={"contentHeading"} />
          <ReactQuill
            theme="snow"
            defaultValue={pageContent2.contentHeading}
            onChange={(e) => updateContent1Data(e, "contentHeading")}
            placeholder={"Write something awesome..."}
            modules={modules("contentHeading")}
            formats={formats}
          />
        </Col>
      </Row>
      <Row>
        <Col md={6}>Content Description : </Col>
        <Col md={6}>
          <EditorToolbar toolbarId={"contentData"} />
          <ReactQuill
            theme="snow"
            defaultValue={pageContent2.contentData}
            onChange={(e) => updateContent1Data(e, "contentData")}
            placeholder={"Write something awesome..."}
            modules={modules("contentData")}
            formats={formats}
          />
        </Col>
      </Row>
      <Row>
        <Col md={6}>Attachment : </Col>
        <Col md={6}>
          <Row>
            {file ? (
              <Col md={6}>
                <img
                  src={URL.createObjectURL(file)}
                  alt=""
                  className="profile_pic"
                />
              </Col>
            ) : (
              <Col md={6}>
                {pageContent2.attachment ? (
                  <>
                    {["jpg", "jpeg", "png", "gif"].includes(
                      pageContent2.attachment.split(".")[1].toLowerCase()
                    ) ? (
                      <img
                        src={`/uploads/${pageContent2.attachment}`}
                        alt={pageContent2.contentHeading}
                        className="profile_pic"
                      />
                    ) : (
                      <a
                        href={`/uploadedFiles/${pageContent2.attachment}`}
                        alt={pageContent2.contentHeading}
                        style={{ maxHeight: "80px" }}
                      >
                        Download Attachment
                      </a>
                    )}
                  </>
                ) : (
                  <img
                    src="/images/no-image-banner.jpg"
                    alt="No Pic Available"
                    className="profile_pic"
                  />
                )}
              </Col>
            )}
            <Col md={6}>
              <div className="position-relative">
                {file ? (
                  <>
                    <p className="pt-3">{file.name}</p>
                    <FontAwesomeIcon
                      icon={faRotateBack}
                      onClick={handleFileRemove}
                      className="mt-1 edit-icon"
                    />
                  </>
                ) : (
                  <input
                    type="file"
                    name="page1content"
                    id="page1content"
                    onChange={handleFileChange}
                    ref={fileInputRef}
                  />
                )}
              </div>
            </Col>
          </Row>
        </Col>
      </Row>
      <div>
        <Row>
          <Col className="text-end">
            <Button
              className="sp-btn-secondary rounded-0 px-5 mx-2"
              onClick={cancelEditContent2}
            >
              Cancel
            </Button>
            <Button
              className="sp-btn-secondary rounded-0 px-5"
              onClick={updateHomeContent1}
            >
              Update
            </Button>
          </Col>
        </Row>
      </div>
    </>
  );
};

export default EditHomeContent2;
