import React from "react";
import { Col, Row } from "reactstrap";
import parse from "html-react-parser";

const HomeTopBanner = ({ homeData }) => {
  const banner = homeData.banner;
  return (
    <>
      <Row>
        <Col md={6}>Status : </Col>
        <Col md={6}>{banner.active ? "Active" : "Inactive"}</Col>
      </Row>
      <Row>
        <Col md={6}>Banner Image : </Col>
        <Col md={6}>
          {banner.bannerImage ? (
            <img
              src={`/uploads/${banner.bannerImage}`}
              alt="UI/UX Design"
              style={{ maxHeight: "80px" }}
            />
          ) : (
            <img
              src={`/images/no-image-banner.jpg`}
              alt="UI/UX Design"
              style={{ maxHeight: "80px" }}
            />
          )}
        </Col>
      </Row>
      <Row>
        <Col md={6}>Banner Intro Line 1 : </Col>
        <Col md={6}>{parse(`${banner.introductionL1}`)}</Col>
      </Row>
      <Row>
        <Col md={6}>Banner Intro Line 2 : </Col>
        <Col md={6}>{parse(`${banner.introductionL2}`)}</Col>
      </Row>
    </>
  );
};

export default HomeTopBanner;
