import React, { useRef, useState } from "react";
import ReactQuill from "react-quill";
import EditorToolbar, { modules, formats } from "../utils/QuillToolbar";
import "react-quill/dist/quill.snow.css";
import { Button, Col, Input, Row } from "reactstrap";
import { useDispatch, useSelector } from "react-redux";
import { updateProfileReducer } from "../reducers/authReducer";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faRemove } from "@fortawesome/free-solid-svg-icons";
import axios from "axios";
const API_URL = process.env.REACT_APP_API_URL
  ? process.env.REACT_APP_API_URL
  : "";

const EditUserProfile = (props) => {
  const dispatch = useDispatch();
  const { userdetails } = useSelector((state) => state.user);
  const [updatedProfile, setUpdatedProfile] = useState({
    ...props.userProfile,
  });
  const [file, setFile] = useState(null);
  const fileInputRef = useRef(null);
  const [message, setMessage] = useState("");

  const updateProfileData = async (e, field) => {
    if (field) {
      await setUpdatedProfile({
        ...updatedProfile,
        [field]: e,
      });
    } else {
      await setUpdatedProfile({
        ...updatedProfile,
        [e.target.id]: e.target.value,
      });
    }
  };

  const cancelEditProfile = () => {
    props.cancelEditProfile(false);
  };

  const updateUserProfile = async () => {
    const formData = new FormData();
    formData.append("image", file);
    try {
      if (file) {
        const res = await axios.post(`${API_URL}/api/upload/`, formData, {
          headers: {
            "Content-Type": "multipart/form-data",
            token: userdetails.token,
          },
        });
        await setUpdatedProfile({
          ...updatedProfile,
          pic: res.data.filename,
        });
        const { data } = await dispatch(
          updateProfileReducer({ ...updatedProfile, pic: res.data.filename })
        );
      } else {
        const { data } = await dispatch(
          updateProfileReducer({ ...updatedProfile })
        );
      }

      props.cancelEditProfile(false);
    } catch (err) {
      setMessage(err.response.data.message);
    }
  };

  const handleFileChange = (event) => {
    setFile(event.target.files[0]);
  };
  const handleFileRemove = (event) => {
    setFile(null);
    fileInputRef.current.value = "";
  };

  return (
    <div>
      <Row>
        <Col md={2}></Col>
        <Col md={10}>
          <div className="position-relative">
            <Row>
              {file ? (
                <Col md={6}>
                  <img
                    src={URL.createObjectURL(file)}
                    alt={updatedProfile.name}
                    className="profile_pic"
                  />
                </Col>
              ) : (
                <Col md={6}>
                  {updatedProfile.pic ? (
                    <img
                      src={`/uploads/${updatedProfile.pic}`}
                      alt={updatedProfile.name}
                      className="profile_pic"
                    />
                  ) : (
                    <img
                      src="/uploads/user-avatar.jpg"
                      alt="No Profile Pic Available"
                      className="profile_pic"
                    />
                  )}
                </Col>
              )}
              <Col md={6}>
                <input
                  type="file"
                  name="profilePic"
                  id="profilePic"
                  onChange={handleFileChange}
                  ref={fileInputRef}
                />
                <FontAwesomeIcon
                  icon={faRemove}
                  onClick={handleFileRemove}
                  className="mt-1 edit-icon"
                />
              </Col>
            </Row>
            <Row>
              <Col md={6}>Name :</Col>
              <Col md={6}>
                <Input
                  type="text"
                  placeholder="Enter Profile Name"
                  id="name"
                  defaultValue={updatedProfile.name}
                  onChange={(e) => updateProfileData(e)}
                />
              </Col>
            </Row>
            <Row>
              <Col md={6}>Mobile : </Col>
              <Col md={6}>
                <Row>
                  <Col>
                    <Input
                      type="text"
                      id="mobile"
                      placeholder="Enter Mobile Number"
                      defaultValue={updatedProfile.mobile}
                      onChange={(e) => updateProfileData(e)}
                    />
                  </Col>
                  <Col>
                    <Input
                      type="text"
                      id="mobile2"
                      placeholder="Alternate Mobile Number"
                      defaultValue={updatedProfile.mobile2}
                      onChange={(e) => updateProfileData(e)}
                    />
                  </Col>
                </Row>
              </Col>
            </Row>
            <Row>
              <Col md={6}>D.O.B : </Col>
              <Col md={6}>
                <Input
                  type="text"
                  id="dob"
                  placeholder="Enter DOB"
                  defaultValue={updatedProfile.dob}
                  onChange={(e) => updateProfileData(e)}
                />
              </Col>
            </Row>
            <Row>
              <Col md={6}>Language : </Col>
              <Col md={6}>
                <Input
                  type="text"
                  id="language"
                  placeholder="Enter Language"
                  defaultValue={updatedProfile.language}
                  onChange={(e) => updateProfileData(e)}
                />
              </Col>
            </Row>
            <Row>
              <Col md={6}>Strength : </Col>
              <Col md={6}>
                <Input
                  type="text"
                  id="strength"
                  placeholder="Enter Strength"
                  defaultValue={updatedProfile.strength}
                  onChange={(e) => updateProfileData(e)}
                />
              </Col>
            </Row>
            <Row>
              <Col md={6}>Skype Id : </Col>
              <Col md={6}>
                <Input
                  type="text"
                  id="skypeId"
                  placeholder="Enter SkypeId"
                  defaultValue={updatedProfile.skypeId}
                  onChange={(e) => updateProfileData(e)}
                />
              </Col>
            </Row>
            <Row>
              <Col md={6}>Permanent Address : </Col>
              <Col md={6}>
                <EditorToolbar toolbarId={"permanentAddress"} />
                <ReactQuill
                  theme="snow"
                  defaultValue={updatedProfile.permanentAddress}
                  onChange={(e) => updateProfileData(e, "permanentAddress")}
                  placeholder={"Write something awesome..."}
                  modules={modules("permanentAddress")}
                  formats={formats}
                />
              </Col>
            </Row>
            <Row>
              <Col md={6}>Career Objectives : </Col>
              <Col md={6}>
                <EditorToolbar toolbarId={"careerObjective"} />
                <ReactQuill
                  theme="snow"
                  defaultValue={updatedProfile.careerObjective}
                  onChange={(e) => updateProfileData(e, "careerObjective")}
                  placeholder={"Write something awesome..."}
                  modules={modules("careerObjective")}
                  formats={formats}
                />
              </Col>
            </Row>
            <Row>
              <Col md={6}>Career Summery : </Col>
              <Col md={6}>
                <EditorToolbar toolbarId={"careerSummary"} />
                <ReactQuill
                  theme="snow"
                  defaultValue={updatedProfile.careerSummary}
                  onChange={(e) => updateProfileData(e, "careerSummary")}
                  placeholder={"Write something awesome..."}
                  modules={modules("careerSummary")}
                  formats={formats}
                />
              </Col>
            </Row>
            <Row>
              <Col md={6}>Personal Qualities : </Col>
              <Col md={6}>
                <EditorToolbar toolbarId={"personalQualities"} />
                <ReactQuill
                  theme="snow"
                  defaultValue={updatedProfile.personalQualities}
                  onChange={(e) => updateProfileData(e, "personalQualities")}
                  placeholder={"Write something awesome..."}
                  modules={modules("personalQualities")}
                  formats={formats}
                />
              </Col>
            </Row>
            <Row>
              <Col className="text-end">
                <Button
                  className="sp-btn-secondary rounded-0 px-5 mx-2"
                  onClick={(e) => cancelEditProfile()}
                >
                  Cancel
                </Button>
                <Button
                  className="sp-btn-secondary rounded-0 px-5"
                  onClick={(e) => updateUserProfile()}
                >
                  Update
                </Button>
              </Col>
            </Row>
          </div>
        </Col>
      </Row>
    </div>
  );
};

export default EditUserProfile;
