import React, { useState, useEffect } from "react";
import Base from "../Base";
import { useSelector, useDispatch } from "react-redux";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faEdit } from "@fortawesome/free-solid-svg-icons";
import axios from "axios";
import {
  Col,
  Row,
  Accordion,
  AccordionBody,
  AccordionHeader,
  AccordionItem,
  Spinner,
} from "reactstrap";

import UserProfile from "../shared-components/UserProfile";
import EditUserProfile from "../shared-components/EditUserProfile";
import { ErrorAlert, SuccessAlert } from "../shared-components/AlertBox";
import HomeTopBanner from "../shared-components/HomeTopBanner";
import EditHomeTopBanner from "../shared-components/EditHomeTopBanner";
import { getHomeData } from "../reducers/homeReducer";
import HomeContent1 from "../shared-components/HomeContent1";
import EditHomeContent1 from "../shared-components/EditHomeContent1";
import HomeContent2 from "../shared-components/HomeContent2";
import EditHomeContent2 from "../shared-components/EditHomeContent2";
import EditServices from "../shared-components/EditServices";
import EditSkills from "../shared-components/EditSkills";
import EditMiddleBanner from "../shared-components/EditMiddleBanner";
import MiddleBanner from "../shared-components/MiddleBanner";
const API_URL = process.env.REACT_APP_API_URL
  ? process.env.REACT_APP_API_URL
  : "";
const Profile = () => {
  const { error, loading, success, userdetails } = useSelector(
    (state) => state.user
  );
  const {
    homeScreenError,
    homeScreenLoading,
    homeScreenSuccess,
    homeScreen,
  } = useSelector((state) => state.home);

  const token = userdetails.token;
  const config = {
    headers: { "Content-type": "application/json", token },
  };
  const [editProfile, setEditProfile] = useState(false);
  const [editHomeTopBanner, setEditHomeTopBanner] = useState(false);
  const [editHomeContent1, setEditHomeContent1] = useState(false);
  const [editMiddleBanner, setEditMiddleBanner] = useState(false);
  const [editHomeContent2, setEditHomeContent2] = useState(false);
  const [editServices, setEditServices] = useState(false);
  const [editSkills, setEditSkills] = useState(false);
  const [userProfile, setUserProfile] = useState({});
  const [homeData, setHomeData] = useState({});

  const dispatch = useDispatch();
  const [open, setOpen] = useState("");
  const toggle = (id) => {
    if (open === id) {
      setOpen();
    } else {
      setOpen(id);
    }
  };

  const getHomeScreenData = async () => {
    const data = await dispatch(getHomeData({ token: userdetails.token }));
    if (!data.error) {
      await setHomeData(data.payload);
    } else {
      await setHomeData({});
    }
  };

  useEffect(() => {
    const getUserData = async () => {
      if (userdetails.token) {
        const { data } = await axios.get(
          `${API_URL}/api/user/info/${userdetails.token}`
        );
        if (!data.error) {
          await setUserProfile(data);
        } else {
          await setUserProfile({});
        }
      } else {
        await setUserProfile({});
      }
    };
    getUserData();
    getHomeScreenData();
  }, [userdetails]);

  const cancelEditProfile = (value) => {
    setEditProfile(value);
  };
  const cancelEditTopBanner = (value) => {
    setEditHomeTopBanner(value);
    getHomeScreenData();
  };
  const cancelEditContent1 = (value) => {
    setEditHomeContent1(value);
    getHomeScreenData();
  };
  const cancelEditMiddleBanner = (value) => {
    setEditMiddleBanner(value);
    getHomeScreenData();
  };
  const cancelEditContent2 = (value) => {
    setEditHomeContent2(value);
    getHomeScreenData();
  };
  const cancelEditServices = (value) => {
    setEditServices(value);
    getHomeScreenData();
  };
  const cancelEditSkills = (value) => {
    setEditSkills(value);
    getHomeScreenData();
  };

  return (
    <Base>
      <div className="container-fluid p-md-5 p-color profile">
        <h4>Personal Info: {editProfile ? "true" : "false"}</h4>
        {!editProfile ? (
          <Row>
            <Col md={2}></Col>
            <Col md={10}>
              <div className="position-relative">
                <UserProfile
                  // userdetails={userdetails}
                  userProfile={userProfile}
                />
                <FontAwesomeIcon
                  icon={faEdit}
                  onClick={(e) => setEditProfile(true)}
                  className="mt-1 edit-icon"
                  style={{ padding: "7px 7px" }}
                />
              </div>
            </Col>
          </Row>
        ) : (
          <EditUserProfile
            userProfile={userProfile}
            cancelEditProfile={cancelEditProfile}
          />
        )}

        {homeData && (
          <>
            <h4>Home Page:</h4>
            <Row>
              <Col md={2}></Col>
              <Col md={10}>
                <Accordion
                  className="p-color"
                  flush
                  open={open}
                  toggle={toggle}
                >
                  {homeData.banner && (
                    <AccordionItem>
                      <AccordionHeader targetId="1">
                        Top Banner:
                      </AccordionHeader>
                      <AccordionBody accordionId="1">
                        <div className="position-relative">
                          {!editHomeTopBanner ? (
                            <>
                              <HomeTopBanner homeData={homeData} />
                              <FontAwesomeIcon
                                icon={faEdit}
                                onClick={(e) => setEditHomeTopBanner(true)}
                                className="mt-1 edit-icon"
                                style={{ padding: "7px 7px" }}
                              />
                            </>
                          ) : (
                            <EditHomeTopBanner
                              homeData={homeData}
                              cancelEditTopBanner={cancelEditTopBanner}
                            />
                          )}
                        </div>
                      </AccordionBody>
                    </AccordionItem>
                  )}
                  {homeData.pageContent1 && (
                    <AccordionItem>
                      <AccordionHeader targetId="2">
                        Home Content 1
                      </AccordionHeader>
                      <AccordionBody accordionId="2">
                        <div className="position-relative">
                          {!editHomeContent1 ? (
                            <>
                              <HomeContent1 homeData={homeData} />
                              <FontAwesomeIcon
                                icon={faEdit}
                                onClick={(e) => setEditHomeContent1(true)}
                                className="mt-1 edit-icon"
                                style={{ padding: "7px 7px" }}
                              />
                            </>
                          ) : (
                            <EditHomeContent1
                              homeData={homeData}
                              cancelEditContent1={cancelEditContent1}
                            />
                          )}
                        </div>
                      </AccordionBody>
                    </AccordionItem>
                  )}
                  {homeData.services && (
                    <AccordionItem>
                      <AccordionHeader targetId="3">Services</AccordionHeader>
                      <AccordionBody accordionId="3">
                        <div className="position-relative">
                          {!editServices ? (
                            <>
                              <Row>
                                <Col sx={6}>Status</Col>
                                <Col sx={6}>
                                  {homeData.services.active
                                    ? "Active"
                                    : "Inactive"}
                                </Col>
                              </Row>
                              <FontAwesomeIcon
                                icon={faEdit}
                                onClick={(e) => setEditServices(true)}
                                className="mt-1 edit-icon"
                                style={{ padding: "7px 7px" }}
                              />
                            </>
                          ) : (
                            <EditServices
                              homeData={homeData}
                              cancelEditServices={cancelEditServices}
                            />
                          )}
                        </div>
                      </AccordionBody>
                    </AccordionItem>
                  )}
                  {homeData.middleBanner && (
                    <AccordionItem>
                      <AccordionHeader targetId="4">
                        Middle Banner
                      </AccordionHeader>
                      <AccordionBody accordionId="4">
                        <div className="position-relative">
                          {!editMiddleBanner ? (
                            <>
                              <MiddleBanner homeData={homeData} />
                              <FontAwesomeIcon
                                icon={faEdit}
                                onClick={(e) => setEditMiddleBanner(true)}
                                className="mt-1 edit-icon"
                                style={{ padding: "7px 7px" }}
                              />
                            </>
                          ) : (
                            <EditMiddleBanner
                              homeData={homeData}
                              cancelEditMiddleBanner={cancelEditMiddleBanner}
                            />
                          )}
                        </div>
                      </AccordionBody>
                    </AccordionItem>
                  )}
                  {homeData.pageContent2 && (
                    <AccordionItem>
                      <AccordionHeader targetId="5">
                        Home Content 2
                      </AccordionHeader>
                      <AccordionBody accordionId="5">
                        <div className="position-relative">
                          {!editHomeContent2 ? (
                            <>
                              <HomeContent2 homeData={homeData} />
                              <FontAwesomeIcon
                                icon={faEdit}
                                onClick={(e) => setEditHomeContent2(true)}
                                className="mt-1 edit-icon"
                                style={{ padding: "7px 7px" }}
                              />
                            </>
                          ) : (
                            <EditHomeContent2
                              homeData={homeData}
                              cancelEditContent2={cancelEditContent2}
                            />
                          )}
                        </div>
                      </AccordionBody>
                    </AccordionItem>
                  )}
                  {homeData.skills && (
                    <AccordionItem>
                      <AccordionHeader targetId="6">Skills</AccordionHeader>
                      <AccordionBody accordionId="6">
                        <div className="position-relative">
                          {!editSkills ? (
                            <>
                              <Row>
                                <Col sx={6}>Status</Col>
                                <Col sx={6}>
                                  {homeData.skills.active
                                    ? "Active"
                                    : "Inactive"}
                                </Col>
                              </Row>
                              <FontAwesomeIcon
                                icon={faEdit}
                                onClick={(e) => setEditSkills(true)}
                                className="mt-1 edit-icon"
                                style={{ padding: "7px 7px" }}
                              />
                            </>
                          ) : (
                            <EditSkills
                              homeData={homeData}
                              cancelEditSkills={cancelEditSkills}
                            />
                          )}
                        </div>
                      </AccordionBody>
                    </AccordionItem>
                  )}
                </Accordion>
              </Col>
            </Row>
          </>
        )}
      </div>
      {loading && (
        <span className="position-absolute top-50 start-50 translate-middle">
          <Spinner size="sm" color="primary">
            Loading...
          </Spinner>
        </span>
      )}
      {error && <ErrorAlert message={error} />}
      {success && <SuccessAlert message={success} />}
    </Base>
  );
};

export default Profile;
