import React, { useRef, useState } from "react";
import ReactQuill from "react-quill";
import EditorToolbar, { modules, formats } from "../utils/QuillToolbar";
import "react-quill/dist/quill.snow.css";
import { Button, Col, FormGroup, Input, Row } from "reactstrap";
import { useDispatch, useSelector } from "react-redux";
import { updateHomeReducer } from "../reducers/homeReducer";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faRemove } from "@fortawesome/free-solid-svg-icons";
import axios from "axios";
const API_URL = process.env.REACT_APP_API_URL
  ? process.env.REACT_APP_API_URL
  : "";
const EditHomeTopBanner = (props) => {
  const [bannerData, setBannerData] = useState(props.homeData.banner);
  let homeData = props.homeData;

  const dispatch = useDispatch();
  const { userdetails } = useSelector((state) => state.user);
  const [file, setFile] = useState(null);
  const fileInputRef = useRef(null);
  const [message, setMessage] = useState("");

  const updateTopBannerData = async (e, field) => {
    if (field) {
      await setBannerData({
        ...bannerData,
        [field]: e,
      });
    } else {
      await setBannerData({
        ...bannerData,
        [e.target.id]: e.target.value,
      });
    }
  };

  const cancelEditTopBanner = () => {
    props.cancelEditTopBanner(false);
  };

  const updateHomeTopBanner = async () => {
    const formData = new FormData();
    formData.append("image", file);
    try {
      if (file) {
        const res = await axios.post(`${API_URL}/api/upload/`, formData, {
          headers: {
            "Content-Type": "multipart/form-data",
            token: userdetails.token,
          },
        });
        await setBannerData({
          ...bannerData,
          bannerImage: res.data.filename,
        });
        const { data } = await dispatch(
          updateHomeReducer({
            ...homeData,
            banner: {
              ...bannerData,
              bannerImage: res.data.filename,
            },
            token: userdetails.token,
          })
        );
      } else {
        const { data } = await dispatch(
          updateHomeReducer({
            ...homeData,
            banner: { ...bannerData },
            token: userdetails.token,
          })
        );
      }

      props.cancelEditTopBanner(false);
    } catch (err) {
      setMessage(err.response.data.message);
    }
  };

  const handleFileChange = (event) => {
    setFile(event.target.files[0]);
  };
  const handleFileRemove = (event) => {
    setFile(null);
    fileInputRef.current.value = "";
  };

  return (
    <>
      <Row>
        <Col md={6}>Active : </Col>
        <Col md={6}>
          <FormGroup switch>
            <Input
              role="switch"
              type="switch"
              checked={bannerData.active}
              onChange={() => {
                setBannerData({ ...bannerData, active: !bannerData.active });
              }}
            />
          </FormGroup>
        </Col>
      </Row>
      <Row>
        <Col md={6}>Banner Image : </Col>
        <Col md={6}>
          <Row>
            {file ? (
              <Col md={6}>
                <img
                  src={URL.createObjectURL(file)}
                  alt="Top Banner"
                  className="profile_pic"
                />
              </Col>
            ) : (
              <Col md={6}>
                {bannerData.bannerImage ? (
                  <img
                    src={`/uploads/${bannerData.bannerImage}`}
                    alt="Top Banner"
                    className="profile_pic"
                  />
                ) : (
                  <img
                    src="/images/no-image-banner.jpg"
                    alt="No Profile Pic Available"
                    className="profile_pic"
                  />
                )}
              </Col>
            )}
            <Col md={6}>
              <input
                type="file"
                name="profilePic"
                id="profilePic"
                onChange={handleFileChange}
                ref={fileInputRef}
              />
              <FontAwesomeIcon
                icon={faRemove}
                onClick={handleFileRemove}
                className="mt-1 edit-icon"
              />
            </Col>
          </Row>
        </Col>
      </Row>
      <Row>
        <Col md={6}>Banner Intro Line 1 : </Col>
        <Col md={6}>
          <EditorToolbar toolbarId={"introductionL1"} />
          <ReactQuill
            theme="snow"
            defaultValue={bannerData.introductionL1.split(" ").join("&nbsp;")}
            onChange={(e) => updateTopBannerData(e, "introductionL1")}
            placeholder={"Write something awesome..."}
            modules={modules("introductionL1")}
            formats={formats}
          />
        </Col>
      </Row>
      <Row>
        <Col md={6}>Banner Intro Line 2 : </Col>
        <Col md={6}>
          <EditorToolbar toolbarId={"introductionL2"} />
          <ReactQuill
            theme="snow"
            defaultValue={bannerData.introductionL2.split(" ").join("&nbsp;")}
            onChange={(e) => updateTopBannerData(e, "introductionL2")}
            placeholder={"Write something awesome..."}
            modules={modules("introductionL2")}
            formats={formats}
          />
        </Col>
      </Row>
      <div>
        <Row>
          <Col className="text-end">
            <Button
              className="sp-btn-secondary rounded-0 px-5 mx-2"
              onClick={cancelEditTopBanner}
            >
              Cancel
            </Button>
            <Button
              className="sp-btn-secondary rounded-0 px-5"
              onClick={updateHomeTopBanner}
            >
              Update
            </Button>
          </Col>
        </Row>
      </div>
    </>
  );
};

export default EditHomeTopBanner;
