import React, { useRef, useState } from "react";
import ReactQuill from "react-quill";
import EditorToolbar, { modules, formats } from "../utils/QuillToolbar";
import "react-quill/dist/quill.snow.css";
import { Button, Col, FormGroup, Input, Row } from "reactstrap";
import { useDispatch, useSelector } from "react-redux";
import { updateHomeReducer } from "../reducers/homeReducer";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faRemove, faRotateBack } from "@fortawesome/free-solid-svg-icons";
import axios from "axios";
import { API_URL } from "../utils/API_URl";

const EditHomeContent1 = (props) => {
  const [pageContent1, setPageContent1] = useState(props.homeData.pageContent1);
  let homeData = props.homeData;

  const dispatch = useDispatch();
  const { userdetails } = useSelector((state) => state.user);
  const [file, setFile] = useState(null);
  const fileInputRef = useRef(null);
  const [message, setMessage] = useState("");

  const updateContent1Data = async (e, field) => {
    if (field) {
      await setPageContent1({
        ...pageContent1,
        [field]: e,
      });
    } else {
      await setPageContent1({
        ...pageContent1,
        [e.target.id]: e.target.value,
      });
    }
  };

  const cancelEditContent1 = () => {
    props.cancelEditContent1(false);
  };

  const updateHomeContent1 = async () => {
    const formData = new FormData();
    formData.append("image", file);
    try {
      if (file) {
        if (file === "No File") {
          await axios.delete(
            `${API_URL}/api/deleteFiles/${pageContent1.attachment}`
          );
          await setPageContent1({
            ...pageContent1,
            attachment: null,
          });
          const { data } = await dispatch(
            updateHomeReducer({
              ...homeData,
              pageContent1: {
                ...pageContent1,
                attachment: null,
              },
              token: userdetails.token,
            })
          );
        } else {
          const res = await axios.post(`${API_URL}/api/uploadDoc/`, formData, {
            headers: {
              "Content-Type": "multipart/form-data",
              token: userdetails.token,
            },
          });
          await setPageContent1({
            ...pageContent1,
            attachment: res.data.filename,
          });
          const { data } = await dispatch(
            updateHomeReducer({
              ...homeData,
              pageContent1: {
                ...pageContent1,
                attachment: res.data.filename,
              },
              token: userdetails.token,
            })
          );
        }
      } else {
        const { data } = await dispatch(
          updateHomeReducer({
            ...homeData,
            pageContent1: { ...pageContent1 },
            token: userdetails.token,
          })
        );
      }

      props.cancelEditContent1(false);
    } catch (err) {
      setMessage(err.response.data.message);
    }
  };

  const handleFileChange = (event) => {
    console.log(event.target.files[0]);
    setFile(event.target.files[0]);
  };
  const handleUndoSelection = (event) => {
    setFile(null);
    fileInputRef.value = null;
  };

  const handleFileRemove = (event) => {
    setFile("No File");
    fileInputRef.value = null;
  };

  return (
    <>
      <Row>
        <Col md={6}>Active : </Col>
        <Col md={6}>
          <FormGroup switch>
            <Input
              role="switch"
              type="switch"
              checked={pageContent1.active}
              onChange={() => {
                setPageContent1({
                  ...pageContent1,
                  active: !pageContent1.active,
                });
              }}
            />
          </FormGroup>
        </Col>
      </Row>

      <Row>
        <Col md={6}>Content Title : </Col>
        <Col md={6}>
          <EditorToolbar toolbarId={"contentTitle"} />
          <ReactQuill
            theme="snow"
            defaultValue={pageContent1.contentTitle}
            onChange={(e) => updateContent1Data(e, "contentTitle")}
            placeholder={"Write something awesome..."}
            modules={modules("contentTitle")}
            formats={formats}
          />
        </Col>
      </Row>
      <Row>
        <Col md={6}>Content Heading : </Col>
        <Col md={6}>
          <EditorToolbar toolbarId={"contentHeading"} />
          <ReactQuill
            theme="snow"
            defaultValue={pageContent1.contentHeading}
            onChange={(e) => updateContent1Data(e, "contentHeading")}
            placeholder={"Write something awesome..."}
            modules={modules("contentHeading")}
            formats={formats}
          />
        </Col>
      </Row>
      <Row>
        <Col md={6}>Content Description : </Col>
        <Col md={6}>
          <EditorToolbar toolbarId={"contentData"} />
          <ReactQuill
            theme="snow"
            defaultValue={pageContent1.contentData}
            onChange={(e) => updateContent1Data(e, "contentData")}
            placeholder={"Write something awesome..."}
            modules={modules("contentData")}
            formats={formats}
          />
        </Col>
      </Row>
      <Row>
        <Col md={6}>Attachment : </Col>
        <Col md={6}>
          <Row>
            {file ? (
              <Col md={6}>
                <div
                  className="position-relative d-inline-block w-75"
                  style={{ overflow: "hidden" }}
                >
                  {file.name &&
                  ["jpg", "jpeg", "png", "gif"].includes(
                    file.name.split(".")[1].toLowerCase()
                  ) ? (
                    <img
                      src={URL.createObjectURL(file)}
                      alt=""
                      className="profile_pic"
                    />
                  ) : (
                    <p className="pt-3 text-truncate">{file.name}</p>
                  )}
                  {file != "No File" && (
                    <FontAwesomeIcon
                      icon={faRemove}
                      onClick={handleFileRemove}
                      className="mt-2 edit-icon"
                      style={{ padding: "3px 7px" }}
                    />
                  )}
                </div>
              </Col>
            ) : (
              <Col md={6}>
                <div className="position-relative  d-inline-block w-75">
                  {pageContent1.attachment ? (
                    <>
                      {["jpg", "jpeg", "png", "gif"].includes(
                        pageContent1.attachment.split(".")[1].toLowerCase()
                      ) ? (
                        <>
                          <img
                            src={`/uploads/${pageContent1.attachment}`}
                            alt={pageContent1.contentHeading}
                            className="profile_pic"
                          />
                          <FontAwesomeIcon
                            icon={faRemove}
                            onClick={handleFileRemove}
                            className="mt-1 edit-icon"
                            style={{ padding: "3px 7px" }}
                          />
                        </>
                      ) : (
                        <>
                          <a
                            href={`/uploadedFiles/${pageContent1.attachment}`}
                            alt={pageContent1.contentHeading}
                            style={{ maxHeight: "80px" }}
                          >
                            Download Attachment
                          </a>
                          <FontAwesomeIcon
                            icon={faRemove}
                            onClick={handleFileRemove}
                            className="mt-0 edit-icon"
                            style={{ padding: "3px 7px" }}
                          />
                        </>
                      )}
                    </>
                  ) : (
                    <img
                      src="/images/no-image-banner.jpg"
                      alt="No Pic Available"
                      className="profile_pic"
                    />
                  )}
                </div>
              </Col>
            )}
            <Col md={6}>
              <div className="position-relative">
                {file && file != "No File" ? (
                  <>
                    <p className="pt-3 text-truncate">{file.name}</p>
                    <FontAwesomeIcon
                      icon={faRotateBack}
                      onClick={handleUndoSelection}
                      className="mt-2 edit-icon"
                      style={{ padding: "4px" }}
                    />
                  </>
                ) : (
                  <input
                    type="file"
                    name="page1content"
                    id="page1content"
                    onChange={handleFileChange}
                    ref={fileInputRef}
                  />
                )}
              </div>
            </Col>
          </Row>
        </Col>
      </Row>
      <div>
        <Row>
          <Col className="text-end">
            <Button
              className="sp-btn-secondary rounded-0 px-5 mx-2"
              onClick={cancelEditContent1}
            >
              Cancel
            </Button>
            <Button
              className="sp-btn-secondary rounded-0 px-5"
              onClick={updateHomeContent1}
            >
              Update
            </Button>
          </Col>
        </Row>
      </div>
    </>
  );
};

export default EditHomeContent1;
