import React, { useRef, useState } from "react";
import ReactQuill from "react-quill";
import EditorToolbar, { modules, formats } from "../utils/QuillToolbar";
import "react-quill/dist/quill.snow.css";
import { Button, Col, FormGroup, Input, Row } from "reactstrap";
import { useDispatch, useSelector } from "react-redux";
import { updateHomeReducer } from "../reducers/homeReducer";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faRemove } from "@fortawesome/free-solid-svg-icons";
import axios from "axios";
const API_URL = process.env.REACT_APP_API_URL
  ? process.env.REACT_APP_API_URL
  : "";

const EditMiddleBanner = (props) => {
  const [middleBanner, setMiddleBanner] = useState(props.homeData.middleBanner);
  let homeData = props.homeData;

  const dispatch = useDispatch();
  const { userdetails } = useSelector((state) => state.user);
  const [file, setFile] = useState(null);
  const fileInputRef = useRef(null);
  const [message, setMessage] = useState("");

  const updateContent1Data = async (e, field) => {
    if (field) {
      await setMiddleBanner({
        ...middleBanner,
        [field]: e,
      });
    } else {
      await setMiddleBanner({
        ...middleBanner,
        [e.target.id]: e.target.value,
      });
    }
  };

  const cancelEditMiddleBanner = () => {
    props.cancelEditMiddleBanner(false);
  };

  const updateMiddleBanner = async () => {
    const formData = new FormData();
    formData.append("image", file);
    try {
      if (file) {
        const res = await axios.post(`${API_URL}/api/upload/`, formData, {
          headers: {
            "Content-Type": "multipart/form-data",
            token: userdetails.token,
          },
        });
        await setMiddleBanner({
          ...middleBanner,
          bannerImage: res.data.filename,
        });
        const { data } = await dispatch(
          updateHomeReducer({
            ...homeData,
            middleBanner: {
              ...middleBanner,
              bannerImage: res.data.filename,
            },
            token: userdetails.token,
          })
        );
      } else {
        const { data } = await dispatch(
          updateHomeReducer({
            ...homeData,
            middleBanner: { ...middleBanner },
            token: userdetails.token,
          })
        );
      }

      props.cancelEditMiddleBanner(false);
    } catch (err) {
      setMessage(err.response.data.message);
    }
  };

  const handleFileChange = (event) => {
    setFile(event.target.files[0]);
  };
  const handleFileRemove = (event) => {
    setFile(null);
    fileInputRef.current.value = "";
  };

  return (
    <>
      <Row>
        <Col md={6}>Active : </Col>
        <Col md={6}>
          <FormGroup switch>
            <Input
              role="switch"
              type="switch"
              checked={middleBanner.active}
              onChange={() => {
                setMiddleBanner({
                  ...middleBanner,
                  active: !middleBanner.active,
                });
              }}
            />
          </FormGroup>
        </Col>
      </Row>

      <Row>
        <Col md={6}>Banner Content : </Col>
        <Col md={6}>
          <EditorToolbar toolbarId={"bannerText"} />
          <ReactQuill
            theme="snow"
            defaultValue={middleBanner.bannerText}
            onChange={(e) => updateContent1Data(e, "bannerText")}
            placeholder={"Write something awesome..."}
            modules={modules("bannerText")}
            formats={formats}
          />
        </Col>
      </Row>

      <Row>
        <Col md={6}>Banner Image : </Col>
        <Col md={6}>
          <Row>
            {file ? (
              <Col md={6}>
                <img
                  src={URL.createObjectURL(file)}
                  alt=""
                  className="profile_pic"
                />
              </Col>
            ) : (
              <Col md={6}>
                {middleBanner.bannerImage ? (
                  <img
                    src={`/uploads/${middleBanner.bannerImage}`}
                    alt={middleBanner.contentHeading}
                    className="profile_pic"
                  />
                ) : (
                  <img
                    src="/images/no-image-banner.jpg"
                    alt="No Pic Available"
                    className="profile_pic"
                  />
                )}
              </Col>
            )}
            <Col md={6}>
              <input
                type="file"
                name="bannerImage"
                id="bannerImage"
                onChange={handleFileChange}
                ref={fileInputRef}
              />
              <FontAwesomeIcon
                icon={faRemove}
                onClick={handleFileRemove}
                className="mt-1 edit-icon"
              />
            </Col>
          </Row>
        </Col>
      </Row>
      <div>
        <Row>
          <Col className="text-end">
            <Button
              className="sp-btn-secondary rounded-0 px-5 mx-2"
              onClick={cancelEditMiddleBanner}
            >
              Cancel
            </Button>
            <Button
              className="sp-btn-secondary rounded-0 px-5"
              onClick={updateMiddleBanner}
            >
              Update
            </Button>
          </Col>
        </Row>
      </div>
    </>
  );
};

export default EditMiddleBanner;
